.projects {
    padding: 80px 20px;
    background: linear-gradient(to bottom, #1b1f26, #23272f); /* Harmonischer Hintergrund */
    color: white;
    text-align: center;
}

.projects-title {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: var(--primary, #007bff); /* Standardfarbe: Blau */
    text-transform: uppercase;
    letter-spacing: 2px;
}

.project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); /* Flexible Spalten */
    gap: 20px; /* Abstand zwischen den Kacheln */
    justify-content: center;
    align-items: stretch; /* Gleiche Höhe der Kacheln */
}

.project-card {
    background-color: #2a2e38;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 100%;
    position: relative; /* Für den Button-Fokus */
}

.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.5);
}

.project-image {
    width: 100%;
    height: 200px;
    object-fit: cover; /* Proportionales Zuschneiden */
    border-bottom: 3px solid var(--primary, #007bff); /* Standardfarbe */
}

.project-content {
    padding: 20px;
    width: 100%;
    flex: 1; /* Nutzt verbleibenden Platz */
}

.project-content h3 {
    font-size: 1.4em;
    margin-bottom: 10px;
    color: white;
    text-transform: capitalize;
}

.project-description {
    font-size: 1em;
    line-height: 1.6;
    color: #eaeaea;
    margin-bottom: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; /* Keine Umbrüche */
}

.btn.primary {
    display: inline-block;
    padding: 10px 25px;
    border-radius: 25px;
    font-size: 1em;
    font-weight: bold;
    color: white;
    background-color: var(--primary, #007bff);
    text-decoration: none;
    transition: all 0.3s ease;
    box-shadow: 0 8px 15px rgba(0, 123, 255, 0.3);
    position: absolute; /* Button fixiert */
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
}

.btn.primary:hover {
    background-color: var(--primary-hover, #0056b3);
    transform: translate(-50%, -3px);
    box-shadow: 0 15px 25px rgba(0, 123, 255, 0.5);
}

.btn.disabled {
    background-color: #555;
    box-shadow: none;
    cursor: not-allowed;
}

@media (max-width: 768px) {
    .projects {
        padding: 60px 10px;
    }

    .projects-title {
        font-size: 2em;
    }

    .project-card {
        flex-direction: column;
        text-align: center;
    }

    .project-image {
        height: 150px;
    }

    .project-description {
        white-space: normal; /* Ermöglicht Zeilenumbrüche auf kleineren Bildschirmen */
    }
}
