.hero {
    position: relative;
    color: white;
    text-align: center;
    padding: 300px 20px; /* Reduzierter Abstand für besseren Fokus */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.hero-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    filter: brightness(0.9); /* Noch etwas heller für Kontrast */
}

.hero-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)); /* Sanfterer Verlauf */
    z-index: 0;
}

.hero-content {
    position: relative;
    z-index: 1;
    max-width: 1000px;
    text-align: center;
    padding: 20px;
    animation: fadeIn 1.2s ease-in-out; /* Sanfte Einblendanimation */
}

.hero h1 {
    font-size: 4em;
    font-weight: 900;
    margin-bottom: 15px;
    line-height: 1.2;
    text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.5);
    animation: slideInDown 1.2s ease; /* Header-Animation */
}

.hero p {
    font-size: 1.5em;
    font-weight: 300;
    margin-bottom: 30px;
    line-height: 1.6;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.4);
    animation: fadeIn 1.5s ease-in-out; /* Animation für die Beschreibung */
}

.hero-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    animation: fadeIn 2s ease; /* Knöpfe animiert */
}

.hero-buttons a {
    display: inline-block;
    padding: 14px 28px;
    font-size: 1.2em;
    font-weight: bold;
    color: white;
    background: linear-gradient(90deg, #007bff, #0056b3);
    border-radius: 8px; /* Leicht abgerundete Ecken */
    text-decoration: none;
    transition: transform 0.3s ease, background-color 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.3); /* Standard-Schatten */
}

.hero-buttons a:hover {
    background: linear-gradient(90deg, #0056b3, #003f87);
    transform: scale(1.08); /* Leicht vergrößerter Hover-Effekt */
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5); /* Intensiverer Schatten */
}

.hero-icons {
    margin-top: 50px; /* Mehr Platz über den Icons */
    display: flex;
    justify-content: center;
    gap: 30px; /* Größerer Abstand zwischen den Icons */
    animation: fadeInUp 2s ease; /* Icons einblenden */
}

.hero-icon {
    font-size: 2.5em;
    color: white;
    transition: transform 0.3s ease, color 0.3s ease, box-shadow 0.3s ease;
}

.hero-icon:hover {
    color: #007bff;
    transform: scale(1.2); /* Vergrößertes Icon beim Hover */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4); /* Schatteneffekt beim Hover */
}

/* Animationen */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideInDown {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes fadeInUp {
    from {
        transform: translateY(50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* Responsives Design */
@media (max-width: 768px) {
    .hero {
        padding: 200px 20px;
    }

    .hero h1 {
        font-size: 3em;
    }

    .hero p {
        font-size: 1.2em;
    }

    .hero-buttons a {
        padding: 12px 24px;
        font-size: 1em;
    }

    .hero-icons {
        gap: 20px;
    }

    .hero-icon {
        font-size: 2em;
    }
}
