.about {
    background: linear-gradient(to bottom, #1b1f26, #23272f); /* Subtiler Farbverlauf */
    padding: 80px 20px; /* Abstand um die Sektion */
    color: white; /* Standardfarbe für Text */
    text-align: center; /* Zentrierter Text */
    border-radius: 10px; /* Optional abgerundete Ecken */
}

.about-container {
    max-width: 800px; /* Begrenzte Breite für Lesbarkeit */
    margin: 0 auto; /* Zentriert den Inhalt */
}

.about-title {
    font-size: 2.5em; /* Große Überschrift */
    font-weight: 700;
    color: var(--primary); /* Primärfarbe */
    margin-bottom: 10px; /* Abstand unter der Überschrift */
}

.about-subtitle {
    font-size: 1.2em;
    color: #a1a1a1; /* Dezente graue Farbe */
    margin-bottom: 20px;
}

.about-image {
    width: 150px; /* Breite und Höhe des Profilbildes */
    height: 150px;
    border-radius: 50%; /* Rundes Bild */
    object-fit: cover; /* Passt das Bild an */
    margin: 20px auto; /* Abstand und Zentrierung */
    border: 3px solid var(--primary); /* Rahmen um das Bild */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); /* Schatteneffekt */
}

.about-text {
    font-size: 1.2em;
    line-height: 1.6; /* Erhöhte Zeilenhöhe */
    color: #eaeaea; /* Dezenter heller Text */
    margin-bottom: 20px; /* Abstand zwischen Absätzen */
}

/* Responsive Anpassungen */
@media (max-width: 768px) {
    .about {
        padding: 60px 10px; /* Weniger Abstand auf mobilen Geräten */
    }

    .about-title {
        font-size: 2em; /* Kleinere Schriftgröße für mobile Geräte */
    }

    .about-text {
        font-size: 1.1em; /* Angepasste Schriftgröße */
    }
}
