/* Gesamte Wrapper-Struktur */
.contact-form-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #10141e; /* Dunkler Hintergrund */
    padding: 20px;
    background: linear-gradient(135deg, #10141e, #1a1f2b); /* Subtiler Verlauf */
    background-image: url('https://www.transparenttextures.com/patterns/dark-mosaic.png'); /* Subtiles Muster */
    background-blend-mode: multiply;
}

/* Kontaktformular-Box */
.contact-form {
    width: 100%;
    max-width: 500px;
    background: #1b1f26; /* Dunkles Formular */
    padding: 30px;
    border-radius: 16px; /* Größere Rundungen */
    box-shadow: 0 6px 24px rgba(0, 0, 0, 0.3); /* Tieferer Schatten */
    text-align: center;
    animation: fadeIn 0.6s ease-in-out; /* Einblend-Animation */
}

/* Titel des Formulars mit Icon */
.contact-form h2 {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px; /* Abstand zwischen Icon und Text */
    font-size: 28px;
    color: #ffffff; /* Weißer Text */
    margin-bottom: 20px;
    font-family: 'Poppins', sans-serif; /* Professionelle Schriftart */
}

/* Form-Gruppen */
.form-group {
    margin-bottom: 20px;
    text-align: left;
}

/* Labels mit Icon */
.form-group label {
    display: flex;
    align-items: center;
    font-size: 16px;
    margin-bottom: 8px;
    color: #cfcfcf; /* Heller Text für Labels */
    font-family: 'Poppins', sans-serif; /* Einheitliche Schriftart */
    gap: 8px; /* Abstand zwischen Icon und Text */
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 12px;
    font-size: 14px;
    border: 1px solid #444; /* Dunklere Rahmenfarbe */
    border-radius: 6px;
    background: #2a2e38; /* Sehr dunkles Eingabefeld */
    color: #ffffff; /* Weißer Text in Eingabefeldern */
    transition: border-color 0.3s ease, background 0.3s ease;
}

/* Fokus-Effekte für Eingabefelder */
.form-group input:focus,
.form-group textarea:focus {
    border-color: #007bff; /* Blau bei Fokus */
    outline: none;
    background: #333941; /* Leicht aufgehellt bei Fokus */
    box-shadow: 0 0 8px rgba(0, 123, 255, 0.5); /* Glühender Fokus-Effekt */
}

/* Textarea-Spezifische Styles */
.form-group textarea {
    resize: none;
    min-height: 120px;
}

/* Button mit Icon */
button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px; /* Abstand zwischen Icon und Text */
    width: 100%;
    padding: 12px;
    font-size: 16px;
    font-weight: bold;
    color: #ffffff;
    background: linear-gradient(90deg, #007bff, #0056b3); /* Farbverlauf */
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: transform 0.2s ease, background-color 0.3s ease;
}

/* Button Hover-Effekte */
button:hover {
    transform: scale(1.05); /* Leichtes Vergrößern bei Hover */
    background: linear-gradient(90deg, #0056b3, #003f87); /* Etwas dunklerer Verlauf */
}

/* Button: Wenn deaktiviert (z. B. beim Laden) */
button:disabled {
    background-color: #444;
    cursor: not-allowed;
    opacity: 0.7;
}

/* Ladeanimation für den Button */
button.loading {
    position: relative;
    pointer-events: none;
}

button.loading::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 18px;
    border: 3px solid #fff;
    border-radius: 50%;
    border-top-color: transparent;
    animation: spin 0.8s linear infinite;
    transform: translate(-50%, -50%);
}

/* Erfolgs- und Fehlermeldungen */
.success-message {
    margin-top: 20px;
    color: #4caf50; /* Grün für Erfolg */
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.success-message::before {
    content: '✔'; /* Erfolgssymbol */
    font-size: 18px;
}

.error-message {
    margin-top: 20px;
    color: #f44336; /* Rot für Fehler */
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

.error-message::before {
    content: '✖'; /* Fehlersymbol */
    font-size: 18px;
}

/* Checkbox-Gruppe für Datenschutz */
.checkbox-group {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 15px;
}

.checkbox-group input[type="checkbox"] {
    accent-color: #007bff; /* Farbe für das Häkchen */
    width: 18px;
    height: 18px;
    cursor: pointer;
}

.checkbox-group label {
    font-size: 14px;
    color: #cfcfcf;
}

.legal-link {
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
    font-size: 14px;
    padding: 0;
}

.legal-link:hover {
    color: #0056b3;
}

/* Einblend-Animation */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Ladeanimation */
@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Responsives Design für Mobilgeräte */
@media (max-width: 768px) {
    .contact-form {
        padding: 20px;
    }

    button {
        font-size: 14px;
        padding: 10px;
    }
}
